.loginModalBg{
    overflow: auto;
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    background: #000;
    opacity: 0.6;
}
.reviewModalBg{
    overflow: auto;
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    background: #000;
}
.loginModalPopup {
    width: 500px;
    height: 450px;
    background: white; 
    position: absolute;
    display: block;
    left: 50%;
    top: 50%;
    padding: 30px;
    box-sizing: border-box;
    transform: translate(-50%, -50%); 
    font-weight: 600;
}
.reviewModalPopup {
    width: 700px;
    height: 540px;
    background: white; 
    position: absolute;
    display: block;
    left: 50%;
    top: 50%;
    padding: 30px;
    box-sizing: border-box;
    transform: translate(-50%, -50%); 
    font-weight: 600;
}
.loginModalXBtn {
    width: 500px;
    height: 10px;
    color: white;
    cursor: pointer;
}
.reviewModalXBtn {
    position: relative;
    width: 500px;
    height: 10px;
    font-size: 20pt;
    color: white;
    cursor: pointer;
}
.loginModalPopup .loginModalXBtn {
    position: absolute;
    right: 13px;
    top: -10px;
    cursor: pointer;
    width: 50px;
    height: 50px;
    text-align: right;
    font-size: 22px;
}
.loginModalFormId {
    width: 300px;
    height: 40px;
}
.loginModalFormPwd {
    width: 300px;
    height: 40px;
}
.loginLink {
    text-decoration: none;
    color: gray;
}
.loginLink:hover {
    text-decoration: underline;
}
#reviewForm legend {
    font-size: 13pt;
} 

#reviewForm fieldset{
    display: inline-block; /*       .*/
    border: 0; /*    */
}
#reviewForm input[type=radio]{
    display: none; /*   */
}
#reviewForm #starLabel{
    font-size: 3em; /*   */
    color: transparent; /*     */
    text-shadow: 0 0 0 #f0f0f0; /*     */
}
#reviewForm #starLabel:hover{
    text-shadow: 0 0 0 gold; /*   */
}
#reviewForm #starLabel:hover ~ #starLabel{
    text-shadow: 0 0 0 gold; /*     */
}
#reviewForm .starRate{
    display: inline-block; /*       .*/
    direction: rtl; /*    */
    border: 0; /*    */
}
#reviewForm .starRate legend{
    text-align: left;
}
#reviewForm input[type=radio]:checked ~ #starLabel{
    text-shadow: 0 0 0 gold; /*    */
}
.reviewBtn {
    border: 0;
    float: right;
    width: 200px;
    height: 50px;
}
.submitBtn {
    border: none;
    width: 70px;
    height: 40px;
    background-color: #8d0707;
    font-weight: 600;
    color: white;
    border-radius: 5px;
    cursor: pointer;
}
.resetBtn {
    border: none;
    width: 70px;
    height: 40px;
    background-color: #8d0707;
    font-weight: 600;
    color: white;
    border-radius: 5px;
    cursor: pointer;
}
#reviewForm #reviewRateDiv {
    font-size: 10pt;
    font-weight: 900;
    color: red;
}
#reviewForm #reviewContentDiv {
    font-size: 10pt;
    font-weight: 900;
    color: red;
}
#reviewForm  #reviewStoryDiv {
    font-size: 10pt;
    font-weight: 900;
    color: red;
}
.loginModalBtn {
    background-color: #8d0707;
    color: white;
    text-align: center;
    width: 310px;
    height: 40px;
    border: none;
    cursor: pointer;
    font-size: 12pt;
}
.loginModalBtn:disabled {
    background-color: lightgrey;
    color: gray;
}
